import react from 'react';
import { useState, useEffect } from 'react';

import { Link } from 'react-router-dom'

import Input from '../components/Input';
import Button from '../components/Button';
import Timer from './Timer';

import itempicker from '../components/ItemPicker.module.css';
import input from '../components/Input.module.css';
import button from '../components/Button.module.css'
import sign_up from '../routes/Sign_up.module.css'

import cellphone from '../img/cellphone.png';

import axios from 'axios';


function PhoneCode({ showModal_value, setShowModal_value, phoneCheck, setPhoneCheck, phoneNumber, setPhoneNumber }) {
    const PHONE = "https://52.79.207.32:9999/alert/phoneAuth";
    const CODE = "https://52.79.207.32:9999/alert/phoneAuthOk"

    //    const    . useeffect        .
    const [phone, setPhone] = useState("");

    const [backCode, setBackCode] = useState("");
    const [code, setCode] = useState("");

    const [disInput, setDisInput] = useState(true);

    const [message, setMessage] = useState("      ");
    const [messageClass, setMessageClass] = useState(sign_up.possible);

    const [timer,setTimer] = useState(false);

    const [btnClass, setBtnClass] = useState(sign_up.next_btn_x);

    useEffect(() => {
        setShowModal_value(showModal_value);
    }, [showModal_value]);      //    showmodal    

    const closeModal = () => {
        setShowModal_value(false);
        setTimer(false);
    };

    const onChange_phone = (event) => {
        
        setPhone(event.target.value);
        console.log(phone);
        console.log(showModal_value);
        
    }
    const onChange_code = (event) => {
        setCode(event.target.value);
        console.log(code);
    }

    const onSubmit_phone = async (event) => {
        event.preventDefault();
        setTimer(false);
        const to = phone;
        const content = "";
        const response = await axios.post(PHONE, { to, content });

        setDisInput(false);
        setBackCode(response.data);
        setMessage("*3   .");
        setMessageClass(sign_up.possible);
        console.log(response.data);
        console.log(" ");
        setTimer(true);
    }
    const onSubmit_code = async (event) => {
        event.preventDefault();
        const Code = String(code);
        const BackCode = String(backCode);
        if (Code === BackCode) {
            console.log(code, " ");
            setMessage("* .");
            setPhoneCheck(true);
            setBtnClass(sign_up.next_btn_o);
            setPhoneNumber(phone);
            setTimer(false);
        }
        else {
            setMessage("*  !");
            setMessageClass(sign_up.impossible);
            setPhoneCheck(false);
            setBtnClass(sign_up.next_btn_x);
            console.log("");
            console.log(Code, BackCode)
        }

    }



    return (
        <div>
            {showModal_value && (
                <div className={itempicker.modal}>
                    <Button className={button.img_buttonX} onClick={closeModal}></Button>
                    <div>
                        <p> </p>
                        <div>
                            <img src={cellphone}></img>
                            <p>        .
                                     <span style={{color:'red'}}>3</span>   .
                            </p>

                            <p></p>
                            <Input id="phone" name="phone" type="number"
                                onChange={onChange_phone}
                                placeholder="  - "></Input>
                            <Button onClick={onSubmit_phone} className={button.select} text=""></Button>
                            
                            <br></br>

                            <p></p>
                            <Input id="code" name="code" type="number" 
                            onChange={onChange_code} 
                            disabled={disInput} 
                            placeholder=" "></Input>
                            {timer&&(
                                <Timer limitTime={180}></Timer>
                            )}
                            
                            <Button onClick={onSubmit_code} className={button.select} text=" "></Button>
                            <br></br>
                            <p className={messageClass}>{message}</p>
                        </div>
                        <Link to='/sign_up_inputs' style={{ textDecoration: 'none' }}>
                            <Button disabled={!phoneCheck} className={btnClass} text=" "></Button>
                        </Link>

                    </div>
                </div>
            )}
        </div>
    );

}

export default PhoneCode;