
import Input from "../components/Input";
import Button from "../components/Button";
import ItemPicker from "../components/ItemPicker";
import CustomDatepicker from "../components/CustomDatepicker";
import Results from "../components/Results";


import { Link } from "react-router-dom";
import { useState, useEffect, useRef } from "react";

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

import home from "./Home.module.css";
import button from "../components/Button.module.css";
import input from "../components/Input.module.css";

import readingGlass from "../img/readingGlass.svg";
import calendar from "../img/calendar.svg"

import axios from "axios";


function Home() {
  const SEARCHURL = "http://52.79.207.32:9999/agriculture/search";

  //  
  const [logined, setLogined] = useState(false);
  const [name, setName] = useState("");

  useEffect(() => {
    const storedTokenData = JSON.parse(sessionStorage.getItem('tokenData'));
    if (storedTokenData) {
      setLogined(true);
      const storedUserData = JSON.parse(sessionStorage.getItem('userData'));
      setName(storedUserData.name);
      console.log("logined");
    }
    else {
      setLogined(false);
      console.log("not logined");
    }
  }, [logined])

  const onClick_logout = (event) => {
    sessionStorage.removeItem("tokenData");
    sessionStorage.removeItem("userData");
    sessionStorage.removeItem("phoneNumberData");
    setLogined(false);
    window.location.href = "/";
  }
  //  

  const [Item1, setItem1] = useState("");
  const [Item2, setItem2] = useState("");
  const [btn2, setBtn2] = useState(true);
  const [Item3, setItem3] = useState("");
  const [btn3, setBtn3] = useState(true);
  const [Item4, setItem4] = useState("");
  const [Item5, setItem5] = useState("");

  const [startDate, setStartDate] = useState(new Date());
  const [stringDate, setStringDate] = useState("");

  const [results, setResults] = useState(false);
  const [noResults, setNoResults] = useState(false);

  const [machineList, setMachineList] = useState([]);

  const [region1, setRegion1] = useState(null);
  const [region2, setRegion2] = useState(null);
  const [region3, setRegion3] = useState(null);
  const [tagValue, settagValue] = useState(null);
  const [machineType, setMachineType] = useState(null);
  const [wantTime, setWantTime] = useState("");

  const [token,setToken]=useState(0);

  const array_region1 = [
    "()",
    "()",
    "()",
    "()",
    "()",
    "()",
    "()",
    "()",
    "",
    "()",
    "()",
    "()",
    "()",
    "()",
    "()",
    "()",
    "()",
  ];

  const array_region2 = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  const array_region3 = ["", ""];
  const array_machine = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  const array_equipment = ["(   .)"];



  const onChange = (event) => event.target.value; //return   {}  

  const onReset = (event) => {
    setItem1("");
    setItem2("");
    setItem3("");
    setItem4("");
    setItem5("");
  };

  const getCurrent = () => {
    const date = new Date();
    const years = date.getFullYear();
    const months = String(date.getMonth() + 1).padStart(2, "0");
    const days = String(date.getDate()).padStart(2, "0");
    return `${years}-${months}-${days}`;
  }

  const onChange_date = (date) => {
    const current_date = new Date();
    if (current_date < date) {
      setStartDate(date);
    }
    else {
      console.log("date failed");
      window.alert("  .")
    }

  };

  useEffect(() => {
    const years = startDate.getFullYear();
    const months = String(startDate.getMonth() + 1).padStart(2, "0");
    const days = String(startDate.getDate()).padStart(2, "0");
    const newStringDate = `${years}-${months}-${days}`;
    setStringDate(newStringDate);
  }, [startDate])

  useEffect(() => {
    console.log(stringDate);
  }, [stringDate]);

  useEffect(() => {
    if (Item1) {
      setBtn2(false);
      if (Item2) {
        setBtn3(false);
      } else {
        setBtn3(true);
      }
    }
    else {
      setBtn2(true);
      setBtn3(true);
    }

  }, [Item1, Item2, Item3])

  const onSubmit = async (event) => {
    event.preventDefault();

    const newRegion1 = event.target.region1.value;
    const newRegion2 = event.target.region2.value;
    const newRegion3 = event.target.region3.value || null;
    const newTagValue = event.target.tagValue.value || null;
    const newMachineType = event.target.machineType.value || null;
    const newWantTime = stringDate ? `${stringDate}T00:00:00` : null;


    setRegion1(newRegion1);
    setRegion2(newRegion2);
    setRegion3(newRegion3);
    settagValue(newTagValue);
    setMachineType(newMachineType);
    setWantTime(newWantTime);
    setResults(true);
    setNoResults(false);


    try {
      setToken(token+1)
      console.log(token);
      const response = await axios.post(SEARCHURL, {
        region1,
        region2,
        region3,
        tagValue,
        machineType,
        wantTime,
      });
      console.log(token);

      if (response.data) {
        console.log("datas are existed");
        setResults(true);
        setMachineList(response.data);
        setNoResults(false);
        console.log(machineList);
      }
      else {
        console.log("datas are not existed");
        setNoResults(true);
      }

    } catch (error) {
      console.log(error);
      console.log("fetch failed");
      setNoResults(true);
    }
  };

  useEffect(()=>{
    console.log(
      region1,
      region2,
      region3,
      tagValue,
      machineType,
      wantTime,
      token,
      'values successed!');
  },[region1,region2,region3,tagValue,machineType,wantTime,token])

  return (
    <div>
      <header>
        <div className={home.font0}>   </div>
        <Link to="/" style={{ textDecoration: "none" }}>
          <div className={home.lets}>  </div>
        </Link>
        <br></br>
      </header>

      {!logined
        ? <div className={home.nav}>
          <div className={home.nav1}>
            <Link to="/login"
              style={{
                textDecoration: "none",
                cursor: "pointer"
              }}>
              <h4 style={{ color: "white" }}></h4>
            </Link>
            <h4 style={{ marginRight: "5px", marginLeft: "5px" }}>/</h4>
            <Link to="/sign_up" style={{ textDecoration: "none" }}>
              <h4 style={{ color: "white" }}></h4>
            </Link>
          </div>
        </div>

        : <div className={home.nav}>
          <div className={home.nav1}>
            <h4 style={{ color: "white", marginRight: '4px' }}>
              <span style={{ color: "yellow" }}>{name}</span>
               .
            </h4>
            <Button className={home.nav_btn} text=""></Button>
            <Button className={home.nav_btn} text=""></Button>
            <Button
              className={home.nav_btn}
              text=""
              onClick={onClick_logout}>
            </Button>
          </div>
        </div>
      }
      <div className={home.apply_search}> </div>

      <div className={home.input_tab}>
        {!logined &&
          <div className={home.limit_box}>
            <p>  </p>
            <Link to='/login' style={{ textDecoration: 'none' }}>
              <Button
                className={home.login_btn}
                text=""
              />
            </Link>
          </div>
        }
        <h2 className={home.label_ps}> </h2>

        <form onSubmit={onSubmit}>
          <div className={home.forms}>
            <div className={home.titles}>
              <p className={home.title}></p>
              <p className={home.required}>*  </p>
            </div>

            <div className={home.datas}>
              <div className={home.input_required}>
                <Input
                  id={input.input}
                  type="text"
                  name="region1"
                  placeholder="//"
                  value={Item1 || ""}
                  readOnly
                  required
                />
                <ItemPicker
                  Array_id={1}
                  itemArray={array_region1}
                  Item={Item1}
                  setItem={setItem1}
                ></ItemPicker>
              </div>
              <p className={home.required}>*  </p>

              <div className={home.input_required}>
                <Input
                  id={input.input}
                  type="text"
                  name="region2"
                  placeholder="//"
                  value={Item2 || ""}
                  readOnly
                  required
                />
                <ItemPicker
                  Array_id={2}
                  itemArray={array_region2}
                  Item={Item2}
                  setItem={setItem2}
                  btn={btn2}
                ></ItemPicker>
              </div>
              <p className={home.required}>*  </p>

              <div className={home.input_required}>
                <Input
                  id={input.input}
                  type="text"
                  name="region3"
                  placeholder=""
                  value={Item3}
                  readOnly
                />
                <ItemPicker
                  Array_id={3}
                  itemArray={array_region3}
                  Item={Item3}
                  setItem={setItem3}
                  btn={btn3}
                ></ItemPicker>
                <br></br>
              </div>
            </div>

            <div className={home.titles}>
              <p className={home.title}></p>
            </div>


            <div className={home.datas}>
              <div className={home.input_search}>
                <Input
                  id={input.search}
                  type="text"
                  name="tagValue"
                  placeholder="*1), 2) "
                  className={input.search_tool}
                />

                <br></br>
              </div>

              <div className={home.input}>
                <Input
                  id={input.input}
                  type="text"
                  name="machineType"
                  placeholder="  "
                  value={Item4}
                  readOnly
                  required
                />
                <ItemPicker
                  Array_id={4}
                  itemArray={array_machine}
                  Item={Item4}
                  setItem={setItem4}
                ></ItemPicker>
                <br></br>
              </div>

              <div className={home.input}>
                <Input
                  id={input.input}
                  type="text"
                  name="equipment"
                  placeholder=" "
                  readOnly
                  required
                />
                <ItemPicker
                  Array_id={5}
                  itemArray={array_equipment}
                  Item={Item5}
                  setItem={setItem5}
                ></ItemPicker>
                <br></br>
              </div>
            </div>

            <div className={home.titles}>
              <p className={home.title}>
                
                <br />
                
              </p>
            </div>


            <div className={home.datas}>
              <div className={home.date1}> </div>
              <Input
                id="date"
                value={stringDate}
                type="text"
                name="wantTime"
                className={input.input_date1}
                placeholder={getCurrent()}
                required
                readOnly
              />
              <DatePicker
                selected={startDate}
                onChange={onChange_date}
                customInput={<CustomDatepicker />}
                style={{
                  gridColumns: '2/4',
                  gridRows: '4/5',
                }}
              />
              <div className={home.date2}>  </div>
              <select name="dayCnt" className={input.date2}>
                <option value='1'>1</option>
                <option value='2'>2</option>
                <option value='3'>3</option>
              </select>
            </div>
          </div>

          {
            noResults &&
            <p className={home.impossible}>   !</p>
          }
          <Button type="submit" text="" className={button.search} />
          <br></br>

          <Button
            onClick={onReset}
            type="reset"
            text=""
            className={button.reset}
          />
          <br></br>
        </form>
      </div>

      {
        results &&
        <Results
          machineList={machineList}
          Item3={Item3}>
        </Results>
      }
    </div>
  );
}
export default Home;
